<template>
	<div class="">
		<div class="table_content_body">
			<div class="table_content_body_left">
				<div v-for="item in leftNav" class="left_nav cursor">
					<router-link :to="'/home/fundManageHome/fundDataHome/'+item.component">
						<i class="icon iconfont" :class="'icon-'+item.icon"></i>
						<span>{{ item.text }}</span>
					</router-link>
				</div>
			</div>
			<div class="table_content_body_right">
				<router-view></router-view>
			</div>
			<div class="clear"> </div>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getRouterListApi } from '../../config/api'
	
	export default {
		name: 'fundDataHome',
		data(){
			return {
				leftNav: [
					{
						component: "motherFund",
						icon: 'jijinshuju-mujijin',
						text: '母基金'
					},
					{
						component: "selfAdministerFund",
						icon: 'jijinshuju-ziguanjijin',
						text: '自管基金'
					},
					{
						component: "subFund",
						icon: 'jijinshuju-zijijin',
						text: '子基金'
					}
				]
			}
		},
		created(){
			this.getRouterList()
		},
		methods: {
			// 获取导航路由数据
			getRouterList(){
				getRouterListApi({
					specMenuId: 3
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.leftNav = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
		}
	}
</script>

<style>

</style>


